import { Component, OnInit } from '@angular/core';
import { FormBuilder,FormControl, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import {User} from '../models/User';


import {HttpService} from '../common/httpService/http.service'




@Component({
  selector: 'app-register-c',
  templateUrl: './register-c.component.html',
  styleUrls: ['./register-c.component.scss']
})
export class RegisterCComponent implements OnInit {
loginForm: FormGroup;
otpForm: FormGroup;
  user: User;

        submitted = false;
        checking = false;
        registered = false;
            loading = false;




  constructor(private formBuilder: FormBuilder,private router: Router,private httpService: HttpService,private toastr: ToastrService) {
      this.user = new User();

  }

  ngOnInit(): void {

   this.loginForm = this.formBuilder.group({
                    username: ['', Validators.required],
                    password: ['', Validators.required],
                    name: ['', Validators.required],
                    deliveryAddress: ['', Validators.required],
                    phone: ['', Validators.required],
                    pinCode:['',Validators.required]



                });

                 this.otpForm = this.formBuilder.group({
                                          otpv: ['', Validators.required]

                   });
  }

  reset(){
    this.loginForm.reset();
  }

  get f()
        { return this.loginForm.controls; }

        get m(){
           return this.otpForm.controls;
           }

    onSubmit() {

                  this.user.username = this.f.username.value;
                  this.user.password = this.f.password.value;
                  this.user.name = this.f.name.value;
                  this.user.deliveryAddress = this.f.deliveryAddress.value;
                  this.user.pinCode = this.f.pinCode.value;
                  this.user.phone = this.f.phone.value;
                  this.user.role = "CUSTOMER";
                  this.user.id = 0;


    this.submitted=true;
    this.registered = true;


                       this.httpService.getOtp(this.f.phone.value).subscribe(
                                   data=>{

                                                           console.log("success");


                                   },
                                      error =>{
                                                           console.log("fail2");



                                      });

                      }






submit()

    {

                  this.httpService.verifyOtp(this.f.phone.value,this.m.otpv.value).subscribe(
                                       data=>{
                                                console.log("success");

                                                this.httpService.signc(this.user).subscribe(

                                                               data=>{
                                                               this.toastr.success("successfully registered");
                                                               this.router.navigate(['/login']);

                                                                },
                                                                 error =>{
                                                                                 console.log("user is already registered");



                                                   });

                                       },
                                          error =>{
                                                this.toastr.error("please Enter valid OTP");



                                          });

      }
      }
