import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder,FormControl, FormGroup, Validators } from '@angular/forms';
import {HttpService} from '../common/httpService/http.service'
import { ToastrService } from 'ngx-toastr';
import {Users} from '../models/Users';



@Component({
  selector: 'app-registers',
  templateUrl: './registers.component.html',
  styleUrls: ['./registers.component.scss']
})
export class RegistersComponent implements OnInit {
loginForm: FormGroup;
otpForm : FormGroup;
        submitted = false;
                      checking = false;
                      registered = false;
  user: Users;

        phone: FormControl;
        businessDomain = new FormControl();
                  BdomainList: string[] = ['Medicines', 'Fruits & Vegetables', 'Grocery & Household', 'Personal & Baby Care', 'Bakery'];



  constructor( private formBuilder: FormBuilder,private router: Router,private httpService: HttpService,private toastr: ToastrService) {
     this.user = new Users();

}

  ngOnInit() {
  this.loginForm = this.formBuilder.group({
                      username: ['', Validators.required],
                      password: ['', Validators.required],
                      name: ['', Validators.required],
                      shopAddress: ['', Validators.required],
                       pinCode: ['', Validators.required],

                     gstNumber: ['', Validators.required],
                       businessDomain: ['', Validators.required],
                       phone: ['', Validators.required]






                  });
   this.otpForm = this.formBuilder.group({
                          otpv: ['', Validators.required]

   });

          ;
  }

  reset(){
    this.loginForm.reset();
  }

  verify(){
    if (this.loginForm.invalid){
            this.router.navigate(['/OTPVerification']);

  }


  }

  get f()
        { return this.loginForm.controls; }

   get m(){
   return this.otpForm.controls;
   }

    onSubmit()

   {
    this.user.username = this.f.username.value;
              this.user.password = this.f.password.value;
              this.user.name = this.f.name.value;
              this.user.shopAddress = this.f.shopAddress.value;
              this.user.pinCode = this.f.pinCode.value;
              this.user.gstNumber = this.f.gstNumber.value;
              this.user.businessDomain = this.f.businessDomain.value;
              this.user.phone = this.f.phone.value;
              this.user.role = "SELLER";
              this.user.id = 0

                          this.registered = true;

                          this.httpService.getOtp(this.f.phone.value).subscribe(
                                      data=>{

                                                              console.log("success");


                                      },
                                         error =>{
                                                              console.log("fail");


                                         });







                 // stop here if form is invalid



    }

    submit()
    {
                  this.httpService.verifyOtp(this.f.phone.value,this.m.otpv.value).subscribe(
                                       data=>{
                                                console.log("success");
                                                 this.httpService.signups(this.user).subscribe(

                                                           data=>{
                                                           this.toastr.success("successfully registered");
                                                        this.router.navigate(['/login']);

                                                           },
                                                         error =>{
                                                    console.log("user is already registered");
                                                });


                                       },
                                          error =>{

                                                this.toastr.error("please Enter valid OTP");


                                          });

      }
      }







