import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder,FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import {HttpService} from '../common/httpService/http.service'
import {UserLogin} from '../models/UserLogin';
import {User} from '../models/User';
import { Roles } from '../enum/Roles';




export interface Role {
  value: string;
  viewValue: string;
}

@Component({
  selector: 'app-register-s',
  templateUrl: './register-s.component.html',
  styleUrls: ['./register-s.component.scss']
})
export class RegisterSComponent implements OnInit {
    loginForm: FormGroup;
        submitted = false;
          IsCorrect = false;
              checking = false;
                user: UserLogin;
                userResponse : User;



         roles: Role[] = [
            {value: 'shopkeeper-0', viewValue: 'Shopkeeper'},
            {value: 'customer-1', viewValue: 'Customer'}
          ];



  constructor( private formBuilder: FormBuilder,private router: Router,private toastr: ToastrService,private httpService: HttpService) {
        this.user = new UserLogin();

  }

  ngOnInit() {
       this.loginForm = this.formBuilder.group({
                  username: ['', Validators.required],
                  password: ['', Validators.required],
                 phone: ['', Validators.required]
              });


  }

  reset(){
    this.loginForm.reset();
  }

      get f()
      { return this.loginForm.controls; }

  onSubmit() {

  this.user.username=this.f.username.value;
  this.user.password = this.f.password.value;
  this.user.phone = this.f.phone.value;

     this.submitted = true;


              // stop here if form is invalid



     this.httpService.login(this.user).subscribe(
     response=>{
            console.log("success");
            this.userResponse = response
       localStorage.setItem('user',JSON.stringify(response))
            if(this.userResponse.role === "CUSTOMER")
              this.router.navigate(['/homePage']);
            else if (this.userResponse.role === "SELLER")
              this.router.navigate(['/product-list']);

     },
        error =>{
              this.toastr.error("You need to register first");

        });

 }
navigate(){
if(this.loginForm.valid)
{
  this.IsCorrect = true;
  this.router.navigate(['/homePage']);
  }
  else
  {
  this.IsCorrect = false;
  }

}


  goToHomePage(){
    this.router.navigate(['/app-home-page']);
  }

route(){
this.router.navigate(['/app-register-c']);
}
}
