import { Component, OnInit } from '@angular/core';
import {Shop} from '../../models/shop';
import {HttpService} from '../../common/httpService/http.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-shop-card',
  templateUrl: './shop-card.component.html',
  styleUrls: ['./shop-card.component.scss']
})
export class ShopCardComponent implements OnInit {

  domain:string;
  title = this.domain;
  userPinCode = '411038';

  shops :Shop[];
  imgIcon ;

  constructor( private http : HttpService,
               private route : ActivatedRoute) { }

  ngOnInit(): void {
    this.domain = this.route.snapshot.paramMap.get("id");
    this.http.getAllShops(this.domain,this.userPinCode).subscribe( response =>{
      console.log(response);
      this.shops = response;
      console.log(this.shops);
    });
    switch (this.domain) {
      case 'Grocery':
        this.imgIcon = 'groceries.png'
        break;

      case 'Medicines':
        this.imgIcon = 'medicines.jpg'
        break;

      case 'Bakery':
        this.imgIcon = 'bakery.jpg'
        break;

      case 'Vegetables':
        this.imgIcon = 'vegetables.jpeg'
        break;

      case 'Personal':
        this.imgIcon = 'personal.jpg'
        break;


    }
  }

}
