import { Component, OnInit } from '@angular/core';
import {Shop} from '../../models/shop';
import {HttpService} from '../../common/httpService/http.service';
import {ProductInOrder} from '../../models/ProductInOrder';
import {Router} from '@angular/router';
import {OrderService} from '../../common/httpService/order.service';
import {Order} from '../../models/Order';
import {OrderStatus} from '../../enum/OrderStatus';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss']
})
export class CartComponent implements OnInit {

  userId:string;
  order : Order;
  shop :any ={
    id : 0,
    name: '',
    items: 0,
    subTotal:0
  };
  shops =[];
  shopname :any;
  shopMap: Map<string,ProductInOrder[]> = new Map<string, ProductInOrder[]>();


  constructor(private http: HttpService,
              private router : Router,
              private orderService : OrderService) { }

  ngOnInit(): void {
    let userInfo = JSON.parse(localStorage.getItem('user'));
    console.log(userInfo);
    this.userId = userInfo.id;
    this.http.getCart(this.userId).subscribe( response =>{
      console.log('Response :')
      console.log(response);
      this.updateCart(response);
    });
  }

  updateCart(response){
    Object.keys(response).forEach((key: Extract<keyof typeof response, string>) => {
      this.shopname = response[key][0].shopName;
      this.shopMap.set(this.shopname,response[key]);
      this.shop = {};
      this.shop.id = key;
      this.shop.name = this.shopname;
      this.shop.items = response[key].length;
      this.shop.subTotal = response[key].reduce(
        (prev, cur) => prev + cur.count * cur.productPrice, 0);
      this.shops.push(this.shop);
    });
    console.log('ShopMap :')
    console.log(this.shopMap);
    console.log('Shops :')
    console.log(this.shops);
  }

  checkout(sellerId :string, shopName: string){
    this.order = new Order();
    this.order.buyerId = parseInt(this.userId);
    this.order.sellerId = parseInt(sellerId);
    this.order.products = this.shopMap.get(shopName);
    this.order.orderStatus = 'New';
    this.shops.forEach( shop => {if(shop.name === shopName)
      this.order.orderAmount = shop.subTotal});
    console.log(this.order);
    this.orderService.createOrder(this.order).subscribe( response => {
      console.log("Order created");
      this.router.navigateByUrl('/orders/'+this.userId);
    });
  }

  cancelProduct(productId, sellerId){
    this.http.removeProductFromCart(this.userId,sellerId,productId).subscribe( response => {
      console.log("Removed");
      console.log(response);
      this.updateCart(response);

    });
  }
  step = 0;

  setStep(index: number) {
    this.step = index;
  }

  nextStep() {
    this.step++;
  }

  prevStep() {
    this.step--;
  }

  hidePrevButton(step){
    if(step === 0 || step === this.shops.length-1)
      return true;
    else
      return false;
  }

  hideNextButton(step){
    if(step === this.shops.length-1)
      return true;
    else
      return false;
  }

}
