import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {Shop} from '../../models/shop';
import {HttpService} from '../../common/httpService/http.service';
import {ProductInfo} from '../../models/productInfo';

@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss']
})
export class CardComponent implements OnInit {

  products;
  categories;
  categoryMap : Map<string,ProductInfo> = new Map<string, ProductInfo>()
  title = '';

  constructor(private route : ActivatedRoute,
              private http : HttpService) { }

  ngOnInit(): void {
    const shopId = this.route.snapshot.paramMap.get("id");
    console.log(shopId);
    this.loadProducts(shopId);

  }

  loadProducts(id){
    this.http.getAllProducts(id).subscribe( response => {
      console.log(response);
      this.categories = Object.keys(response);
      Object.keys(response).forEach((key: Extract<keyof typeof response, string>) => {
        this.categoryMap.set(key,response[key]);
      });
      this.showCategoryProducts(this.categories[0]);
      console.log(this.categories);
      console.log(this.categoryMap);
    });
  }

  showCategoryProducts(category){
    this.title = category;
    this.products = this.categoryMap.get(category);
  }

}
