import { Component, OnInit } from '@angular/core';
import {ProductInfo} from '../../models/productInfo';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import {HttpService} from '../../common/httpService/http.service';



@Component({
  selector: 'app-product-add',
  templateUrl: './product-add.component.html',
  styleUrls: ['./product-add.component.scss']
})
export class ProductAddComponent implements OnInit {
    product = new ProductInfo();


  constructor(private route: ActivatedRoute,private router: Router,private toastr: ToastrService,private httpService: HttpService) { }


 productId: string;
    isEdit = false;

  ngOnInit(): void {

  this.productId = this.route.snapshot.paramMap.get('id');
            if (this.productId) {
                this.isEdit = true;
                }
    }

    onSubmit() {
        this.product.sellerUserId = 1;

    this.httpService.addProduct(this.product).subscribe(
                                       data=>{

                                                  this.toastr.success("Added product successfully");
                                                  this.router.navigate(['/product-list']);


                                       },
                                          error =>{
                                                               console.log("fail2");



                                          });

          }


  ngAfterContentChecked(): void {
              console.log(this.product);
          }

  }


