import { Component, OnInit } from '@angular/core';
import { FormBuilder,FormControl, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import {HttpService} from '../common/httpService/http.service'





@Component({
  selector: 'app-otp-verification',
  templateUrl: './otp-verification.component.html',
  styleUrls: ['./otp-verification.component.scss']
})
export class OtpVerificationComponent implements OnInit {

loginForm: FormGroup;
        submitted = false;


  constructor(private formBuilder: FormBuilder,private router: Router,private toastr: ToastrService,private httpService: HttpService) { }

  ngOnInit(): void {
  this.loginForm = this.formBuilder.group({
                    otpv: ['', Validators.required]
                });
  }
  get f()
        { return this.loginForm.controls; }

    onSubmit() {
            this.submitted = true;
              this.httpService.verifyOtp(this.f.otpv,this.f.otpv).subscribe(
                                   data=>{
                                            this.toastr.success("ypu have successfully registered");
                                                                this.router.navigate(['/login']);


                                   },
                                      error =>{
                                            this.toastr.error("please Enter valid OTP");


                                      });

                      }


}

