import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from "rxjs";
import {UserLogin} from '../../models/UserLogin';
import {User} from '../../models/User';
import {Users} from '../../models/Users';
import {Cart} from '../../models/Cart';
import {ProductInfo} from '../../models/productInfo';



@Injectable({
  providedIn: 'root'
})
export class HttpService {

  constructor(private http: HttpClient) { }

public getOtp(phoneNumber): Observable<any>{
    return this.http.post(`http://localhost:8090/otp/sendOtp?phoneNumber=`+phoneNumber,{});
}

public verifyOtp(phoneNumber,otp):Observable<any>{
 return this.http.post(`http://localhost:8090/otp/verifyOtp?phoneNumber=`+phoneNumber+'&otp='+otp,{});

}


  public getProduct(productId): Observable<any>{
   return this.http.get(`http://localhost:8090/products/`+productId);
  }

  public getAllShops(domain, pinCode): Observable<any>{
    return this.http.get(`http://localhost:8090/getShopsByUserDetails/`+domain+`/`+pinCode);
  }

  public getAllProducts(id): Observable<any>{
    return this.http.get(`http://localhost:8090/products/getProductsByCategory/`+id);
  }

  public addToCart(cartItem:Cart): Observable<any>{
    console.log(cartItem);
    return this.http.post<Cart>(`http://localhost:8090/carts/`, cartItem);
  }

  public getCart(buyerId): Observable<any>{
    return this.http.get(`http://localhost:8090/carts/`+buyerId);
  }

  public removeProductFromCart(buyerId,sellerId, productId): Observable<any>{
    return this.http.get(`http://localhost:8090/carts/deleteProductFromCart/`+buyerId+`/`+sellerId+`/`+productId);
  }


  public login(user: UserLogin): Observable<User> {
    return this.http.post<User>(`http://localhost:8090/loginUser/`,user);
  }

  public signups(user: Users): Observable<Users>{
    return this.http.post<Users>(`http://localhost:8090/users/`,user);
    }

   public signc(user: User):Observable<User>{
       return this.http.post<User>(`http://localhost:8090/users/`,user);

   }
   public addProduct(productInfo: ProductInfo):Observable<ProductInfo>{
          return this.http.post<ProductInfo>(`http://localhost:8090/products/`,productInfo);

      }

   public geOrders(productId): Observable<any> {
       return this.http.get(`http://localhost:8090/products/`+productId);
     }

   public updateProduct(productInfo: ProductInfo):Observable<ProductInfo>{
             return this.http.put<ProductInfo>(`http://localhost:8090/updateProduct/`,productInfo);

         }
   public getProductsForSeller(sellerUserId): Observable<any> {
                return this.http.get(`http://localhost:8090/products/getProductsBySellerId/`+sellerUserId);
              }
 }




