import { Component, OnInit } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {ActivatedRoute} from '@angular/router';
import { OrderStatus } from 'src/app/enum/OrderStatus';
import { Roles } from 'src/app/enum/Roles';
import {JwtResponse} from '../../response/JwtResponse';
import {OrderService} from '../../common/httpService/order.service';
import {Subscription} from 'rxjs';
import {Order} from '../../models/Order';
import {UserService} from '../../common/httpService/user.service';

@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.scss']
})
export class OrderComponent implements OnInit {

  orders: any;
  OrderStatus = OrderStatus;
  currentUser: JwtResponse;
  userId;
  Role = Roles;
      productId: string;

  constructor(private httpClient: HttpClient,
              private orderService: OrderService,
              private userService: UserService,
              private route: ActivatedRoute
  ) {
  }

  querySub: Subscription;

  ngOnInit() {
    this.currentUser = this.userService.currentUserValue;
    this.userId = this.route.snapshot.paramMap.get("id");
    this.productId = this.route.snapshot.paramMap.get('id');

    // this.querySub = this.route.queryParams.subscribe(() => {
      this.update();
    // });

  }

  update() {
    // let nextPage = 1;
    // let size = 10;
    // if (this.route.snapshot.queryParamMap.get('page')) {
    //   nextPage = +this.route.snapshot.queryParamMap.get('page');
    //   size = +this.route.snapshot.queryParamMap.get('size');
    // }
    this.orderService.geOrders(this.productId).subscribe( response => {
      this.orders = response;
      console.log(this.orders);
    });
  }


  cancel(order: Order) {
    this.orderService.cancel(order.orderId).subscribe(res => {
      if (res) {
        order.orderStatus = res.orderStatus;
      }
    });
  }

  finish(order: Order) {
    this.orderService.finish(order.orderId).subscribe(res => {
      if (res) {
        order.orderStatus = res.orderStatus;
      }
    })
  }

  ngOnDestroy(): void {
    this.querySub.unsubscribe();
  }

}
