import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {HomeComponent } from './home/home.component';
import {RegisterSComponent} from './register-s/register-s.component';
import {RegisterCComponent} from './register-c/register-c.component';
import {RegistersComponent} from './registers/registers.component';
import {HomePageComponent} from './components/home-page/home-page.component';
import {CardComponent} from './components/card/card.component';
import {CartComponent} from './components/cart/cart.component';
import { OtpVerificationComponent } from './otp-verification/otp-verification.component';
import {ShopCardComponent} from './components/shop-card/shop-card.component';
import {ProductDetailsComponent} from './components/product-details/product-details.component';
import { ProductListComponent } from './components/product-list/product-list.component';
import { ProductEditComponent } from './components/product-edit/product-edit.component';
import { ProductAddComponent } from './components/product-add/product-add.component';
import {CheckoutDetailsComponent} from './components/checkout-details/checkout-details.component';
import {OrderComponent} from './components/order/order.component';







const routes: Routes = [{
      path: '',
      component: HomeComponent
},
{
      path: 'login',
      component: RegisterSComponent
},
{
       path: 'RegisterCustomer',
       component: RegisterCComponent
 },
 {
          path: 'RegisterShopkeeper',
          component: RegistersComponent
    },
    {
        path: 'OTPVerification',
        component: OtpVerificationComponent
    },
  {
    path: 'homePage',
    component: HomePageComponent
  },
  {
    path: 'category/:id',
    component: CardComponent
  },
  {
    path: 'product-details/:id',
    component: ProductDetailsComponent
  },
  {
    path: 'checkout/:id',
    component : CheckoutDetailsComponent
  },
  {
    path: 'cart',
    component: CartComponent
  },
  {
    path: 'shop-cards/:id',
    component: ShopCardComponent
  },
  {
        path: 'product-list',
        component: ProductListComponent
  },
  {
          path: 'product-edit/:id',
          component: ProductEditComponent
   },
   {
             path: 'product-add/:id',
             component: ProductAddComponent
   },
  {
    path: 'orders/:id',
    component: OrderComponent
  },


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
