import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import {ProductInfo} from '../../models/productInfo';
import { ToastrService } from 'ngx-toastr';
import {HttpService} from '../../common/httpService/http.service';





@Component({
  selector: 'app-product-edit',
  templateUrl: './product-edit.component.html',
  styleUrls: ['./product-edit.component.scss']
})
export class ProductEditComponent implements OnInit {
  product = new ProductInfo();
  selected = this.product.categoryType;
  selected2 = this.product.productStatus;

  constructor(private route: ActivatedRoute,private router: Router,private toastr: ToastrService,private httpService: HttpService) { }

  productId: string;

  ngOnInit(): void {

    this.productId = this.route.snapshot.paramMap.get('id');
    if (this.productId) {
      this.httpService.geOrders(this.productId).subscribe(prod => this.product = prod);
    }

  }

  update() {
    this.httpService.updateProduct(this.product).subscribe(prod => {
        if (!prod) throw new Error();
        this.toastr.success("update your product successfully");

        this.router.navigate(['/product-list']);
      },
      err => {
      });

  }

  onSubmit() {
    this.update();

  }


  ngAfterContentChecked(): void {
    console.log(this.product);
  }

}
